
@media print {
  .Menu {
    display: none;
  }
  hr {
    display: none;    
  }
}

.Menu {
  margin: 0px;
  color: blue;
  background-color:lightgrey;
  font-size: 20px;
  position: sticky;
  z-index: 2;
  top: 0;
  padding-inline-start: 0px;
}

hr {
    margin: 0px;
}

.Menu-selected {
  color: blue;
  background-color: white;
}
